* {
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
  background-color: #e3e9ff;
}

.todo {
  width: 700px;
  margin: 100px auto;
}

.todoList {
  margin-top: 20px;
  padding: 15px;
  font-size: 20px;
  background: #af7eeb;
  color: white;
  display: flex;
  align-items: flex-start;
}

.todoList span {
  margin-left: 30%;
}

.todo-content {
  margin-top: 20px;
  background: white;
  position: relative;
  height: 50vh;
  overflow: scroll;
}

.todo-item {
  padding: 15px;
  border-bottom: 2px solid whitesmoke;
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

/* .todo-item input {
  position: absolute;
  left: 6%;
} */

.todo-item label{
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    width: 100%;
}

.todo-item-label {
  text-decoration: line-through;
}

.btn {
  /* position: absolute;
  right: 6%; */
  border: none;
  background: transparent;
  color: #af7eeb;
  cursor: pointer;
}


.todo-item:hover {
  background: whitesmoke;
}

.addBtn {
  color: #af7eeb;
  cursor: pointer;
  border: none;
  background: transparent;
}

.task {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  background-color: white;
  padding: 10px 20px;

}

.task input,
.task input:focus {
  border: none;
  outline: none;
  width: 600px;
  font-size: 15px;
}
